<template>
<div>
  <Suspense>
    <template #default>
      <spi-news :box="list"/>
    </template>
    <template #fallback>
      <LoaderComponentTwo/>
    </template>
  </Suspense>
</div>
</template>

<script>
import{onMounted,reactive,ref} from "vue";
import axios from 'axios';
import{defineAsyncComponent} from "vue";
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
const SpiNews=defineAsyncComponent(()=>import('@/components/main/SpicialNews/SpiNews'));
import {handleError} from "@/components/composable/handleError";
export default {
  name: "SpicialNews",
  components: {LoaderComponentTwo, SpiNews},
  setup(){
    const showSlider=ref(false)
    const list=reactive([])
   const showNews=onMounted(async()=>{
    await axios.all([
        axios.get('https://api.footballi.net/api/v2/news/817358'),
        axios.get('https://api.footballi.net/api/v2/news/817386'),
        axios.get('https://api.footballi.net/api/v2/news/817343'),
        axios.get('https://api.footballi.net/api/v2/news/817350'),
        axios.get('https://api.footballi.net/api/v2/news/817373'),
        axios.get('https://api.footballi.net/api/v2/news/817351'),
     ])

     .then(axios.spread((...res)=>{
       list.push(res)
     }))
     .catch((error)=>{
       handleError(error)
     })
   })
    return{
      showSlider,
      showNews,
      list,
    }
  }
}
</script>

<style scoped>
#message{
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
</style>