<template>
  <div class="flex flex-row align-center justify-between w-full my-3 md:mt-3">
    <div><p class="text-black font-bold text-start">
      ویدیو ها
    </p></div>
    <div class="flex flex-row">
      <p class="text-slate-600 text-sm font-semibold">بیشتر</p>
      <font-awesome-icon :icon="['fas','angle-left']" class="text-black text-sm"></font-awesome-icon>
    </div>
  </div>
  <div class="bg-black h-0.5 m-1"></div>

<div class="grid grid-cols-6 grid-rows-2 gap-2 lg:grid lg:grid-cols-6 lg:grid-rows-2 lg:gap-2 lg:w-full md:grid md:grid-cols-6 md:grid-rows-2 md:gap-2 md:w-full" style="height: 300px">

  <div class="col-span-2 row-span-1 lg:col-span-2 lg:row-span-1 md:col-span-2 md:row-span-1">
  <Suspense>
    <template #default>
      <about-video nums="0" :list="menu"/>
    </template>
    <template #fallback>
      <LoaderComponent :my-height="fullHeight" :height-one="partHeight" />
    </template>
  </Suspense>
  </div>

  <div class="col-span-2 row-span-1 lg:col-span-2 lg:row-span-1 md:col-span-2 md:row-span-1">
    <Suspense>
      <template #default>
        <about-video nums="1" :list="menu"/>
      </template>
      <template #fallback>
        <LoaderComponent :my-height="fullHeight" :height-one="partHeight" />
      </template>
    </Suspense>
  </div>

  <div class="col-span-2 row-span-1 lg:col-span-2 lg:row-span-1 md:col-span-2 md:row-span-1">
    <Suspense>
      <template #default>
        <about-video nums="2" :list="menu"/>
      </template>
      <template #fallback>
        <LoaderComponent :my-height="fullHeight" :height-one="partHeight" />
      </template>
    </Suspense>
  </div>

  <div class="col-span-3 row-span-1 lg:col-span-3 lg:row-span-1 md:col-span-3 md:row-span-1">
    <Suspense>
      <template #default>
        <two-video  nums="3" :list="menu"/>
      </template>
      <template #fallback>
        <LoaderComponent :my-height="fullHeight" :height-one="partHeight" />
      </template>
    </Suspense>
  </div>

  <div class="col-span-3  row-span-1 lg:col-span-3 lg:row-span-1 md:col-span-3 md:row-span-1">
    <Suspense>
      <template #default>
        <two-video  nums="4" :list="menu"/>
      </template>
      <template #fallback>
        <LoaderComponent :my-height="fullHeight" :height-one="partHeight" />
      </template>
    </Suspense>
  </div>
</div>
</template>

<script>

import{reactive,onMounted,ref} from 'vue'
import{defineAsyncComponent} from "vue";
import {useRoute,useRouter} from 'vue-router'
import{FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import EndPoints from "@/service/EndPoints";
import LoaderComponent from "@/components/composable/LoaderComponent";
import {handleError} from "@/components/composable/handleError";
const AboutVideo=defineAsyncComponent(()=>import('@/components/main/videos/AboutVideo'))
const TwoVideo=defineAsyncComponent(()=>import('@/components/main/videos/TwoVideo'))
export default {
  name: "AllVideos",
  components: {LoaderComponent, TwoVideo, AboutVideo,FontAwesomeIcon},
  setup(){
    const fullHeight=ref(80)
    const partHeight=ref(20)
    const showSlider=ref(false)
    const route=useRoute();
    const router=useRouter();
    const menu=reactive([])
    const total=onMounted(async ()=>{
         await EndPoints.getVideos()
       .then((response)=>{
         if(response.data.success){
          menu.push(response.data.data)
         }
       })
      .catch((error)=>{
       handleError(error)
      })
    })
    return{
      showSlider,
      router,
      route,
      menu,
      total,
      fullHeight,partHeight
    }
  }
}
</script>

<style scoped>
#message{
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
[v-clock]::before{
  content: 'loading...';
}
</style>