<template>
<section class="center-teams p-3">
  <div id="scroll" class="container mx-auto sm:mx-1 max-w-screen-md p-1 myScroll" :style="{height:firstHeight+'px'}">
    <div class="w-full h-auto">
      <div class="flex flex-col space-y-2">
        <p class="text-black font-bold text-start my-2" style="width:100%">جدول رده بندی تیم ها</p>
        <div class="bg-slate-400 h-0.5 mt-1 w-full"></div>
        <div class="flex flex-row justify-end w-full">
          <div><p class="text-black font-bold text-end"> بازی های اخیر</p></div>
          <div><input type="radio" class="border border-1 border-slate-600"></div>
        </div>
        <div class="flex flex-row align-center justify-end w-full">
          <ul class="flex flex-row">
            <li v-for="value in list" :key="value.item" class="text-slate-500 font-bold ml-3" :style="{size:size+'px'}">{{value.item}}</li>
          </ul>
        </div>
        <div class="flex flex-col align-center space-y-1 w-full" >
          <Suspense>
            <template #default>
              <RankTeams :menu="sum"/>
            </template>
            <template #fallback>
              <LoaderComponentTwo/>
            </template>
          </Suspense>
        <div class="flex flex-row justify-start w-full"><span class="one w-5 h-5 bg-violet-300 rounded-sm ml-1"></span><p class="text-black font-bold">لیگ قهرمانان اسیا</p></div>
        <div class="flex flex-row justify-start w-full"><span class="one w-5 h-5 bg-violet-300 rounded-sm ml-1"></span><p class="text-black font-bold">مقدماتی لیگ قهرمان اسیا</p></div>
        <div class="flex flex-row justify-start w-full"><span class="one w-5 h-5 bg-slate-700 rounded-sm ml-1"></span><p class="text-black font-bold">سقوط</p></div>
      </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import{reactive,ref,onMounted} from 'vue';
import{defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
const RankTeams=defineAsyncComponent(()=>import('@/components/main/worldCup/RankTeams'))
export default {
  name: "TableTeams",
  components: {RankTeams, LoaderComponentTwo},
  setup(){

    const firstHeight=ref(600)
    const sizeTable=ref(270);
    const list=reactive([{item:'بازی'},{item: 'برد'},{item:'مساوی'},{item:'باخت'},{item:'تفاضل'},{item:'گل+/-'},{item:'امتیاز'}])
    const size=ref(12)
    const sum=reactive([])
    const result=onMounted(async ()=>{
     await EndPoints.getCompGulfs()
      .then((response)=>{
        if(response.data.success){
          sum.push(response.data.data.standing)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      list,
      size,
      sizeTable,
      firstHeight,
      sum,
      result,
    }
  }
}
</script>

<style scoped>
.myScroll{
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: #fff;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color:grey;
}
#scroll::-webkit-scrollbar-track{
  background-color: lightgray;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
</style>