<template>
<section class="about-football ">
  <div class="container mx-auto p-2 xl:h-[760px] lg:h-[760px] md:mx-1 max-w-screen-lg border border-1 border-slate-300 rounded-md">
    <div class="w-full h-auto">
      <p class="text-black font-semibold text-start">همراه با فوتبالی</p>
      <div class="bg-black h-0.5 m-3"></div>

      <div class="xl:flex-col xl:w-full lg:flex-col lg:space-y-3 lg:w-full md:flex md:flex-row md:items-center md:justify-around md:w-full  md:mx-1 sm:grid sm:grid-cols-1 sm:grid-rows-2 sm:gap-2 sm:w-10/12 sm:mx-auto">
        <div class="md:w-5/12 lg:w-full lg:p-1">
      <v-lazy-image @error="notShow" v-if="showSlider" :src="require('/src/assets/image/ic_default_news_image.png')" alt="" class="w-full h-60"/>
      <v-lazy-image @error="notShow" :src="require('/src/assets/image/first.jpg')" alt="" class="w-full sm:w-10/12 sm:mx-auto sm:h-[310px] h-60"/>
      <div class="flex flex-row align-center justify-between mt-4">
        <div><v-lazy-image @error="notShow" :src="require('/src/assets/image/one.jpg')" alt="" class="w-full h-8"/></div>
        <div><v-lazy-image @error="notShow" :src="require('/src/assets/image/two.png')" alt="" class="w-full h-8"/></div>
        <div><v-lazy-image @error="notShow" :src="require('/src/assets/image/three.png')" alt="" class="w-full h-8"/></div>
      </div>
        </div>

      <div class="flex flex-col space-y-2 mt-5 md:flex md:flex-col  md:w-5/12 xl:flex xl:flex-col xl:space-y-2 lg:flex lg:flex-col lg:space-y-2 lg:w-full lg:mx-1 md:mt-0 sm:w-10/12 sm:mx-auto">
        <div v-for="items in list" :key="items">
          <v-lazy-image @error="notShow" :src="`${items.relatives[8].news_image[0].url}`" alt="" class="w-full sm:w-10/12 sm:mx-auto h-46"/>
          <h1 class="text-black font-bold text-md mt-3" style="font-size: 15px">{{items.relatives[8].title}}</h1>
          <div class="flex flex-row align-center justify-between mt-1">
            <div><p class="text-slate-600 font-semibold" style="font-size: 12px">3 ساعت قبل</p></div>
            <div class="flex flex-row ">
              <font-awesome-icon :icon="['fas','comment']" class="text-black" style="font-size: 12px"></font-awesome-icon>
              <span class="text-black font-bsemibold" style="font-size:12px">{{items.relatives[8].news_comment_count}}</span>
              <font-awesome-icon :icon="['fas','eye']" class="text-black" style="font-size: 12px"></font-awesome-icon>
              <span class="text-slate-600 font-semibold" style="font-size: 12px">{{items.relatives[8].visit}}</span>
            </div>
          </div>
        </div>
      </div>
      </div>
    </div>
  </div>
</section>

</template>

<script>
import{reactive,onMounted,ref} from 'vue'
import EndPoints from "@/service/EndPoints";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import VLazyImage from "v-lazy-image";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AboutFootball",
  components: {FontAwesomeIcon,VLazyImage},
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
   const showSlider=ref(false)
    const list=reactive([])
    function notShow(event){
      event.target.src=notFound.value
    }
    const sum=onMounted(()=>{
      EndPoints.getVideos()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
     showSlider,
      list,
      sum,
      notFound,
      notShow

    }
  }
}
</script>

