<template>
<div>
  <p class="text-md font-extrabold text-start font-bold mb-2">اخرین اخبار</p>
  <div class="flex flex-row align-center justify-between w-full bg-slate-300 p-1 rounded-md">
    <div @click="setCounter(1)" :class="{ grayBg : count === 1 , whiteColor: count === 2}"><button  @click="tabs='AllNews'" type="button" class="outline-none text-white text-center text-sm p-1 rounded-sm">همه</button></div>
    <div @click="setCounter(2)" :class="{grayBg : count === 2 , whiteColor : count === 1}"><button  @click="tabs='InternalNews'" type="button" class="outline-none text-white text-center text-sm p-1 rounded-sm">داخلی</button></div>
    <div @click="setCounter(3)" :class="{grayBg : count === 3 , whiteColor : count === 2}"><button  @click="tabs='ExternalNews'" type="button" class="outline-none text-white text-center text-sm p-1 rounded-sm">خارجی</button></div>
  </div>
  <Transition name="fade" :duration="500" mode="out-in">
  <keep-alive>
    <component :is="tabs"></component>
  </keep-alive>
  </Transition>
    <div>
    </div>
</div>
</template>

<script>
import{defineAsyncComponent} from "vue";
const ExternalNews=defineAsyncComponent(()=>import('@/components/main/ExternalNews'))
const InternalNews=defineAsyncComponent(()=>import('@/components/main/InternalNews'))
const  AllNews=defineAsyncComponent(()=>import('@/components/main/AllNews'))

import{ref} from 'vue'
export default {
  name: "LastNews",
  components:{ExternalNews,InternalNews,AllNews},
  setup(){
    const active=ref(false)
    const tabs=ref('AllNews')
    const count=ref(0)
   function setCounter(data){
      count.value=data;
   }
    return{
      tabs,
      active,
      count,
      setCounter,
    }
  }
}
</script>

<style scoped>
.grayBg{
  background-color: grey;
}
.whiteColor{
  color:#fff;
}
.fade-enter-active,
.fade-leave-active{
  transition: all ease-in-out 0.5s;
}
.fade-enter-from,
.fade-leave-to{
  transition: inherit;
  opacity: 0;
}
</style>