<template>
<section class="last-competition p-3">
  <div id="scroll" class="container mx-auto  p-1 myScroll" :style="{height:size+'px'}">
    <div class="mx-auto p-1">
      <p class="text-slate-600 font-bold text-start m-2">برترین های فصل 1403-1402</p>
      <div class="flex flex-row align-center justify-between bg-slate-300 p-1">
        <div @click="showKey(1)"><button :class="{bgGray : number===1 , textWhite : number===2}" @click="myKey='BestGoals'" type="button" class="text-center text-white font-bold text-sm p-1">گل</button></div>
        <div @click="showKey(2)"><button :class="{bgGray : number===2 , textWhite: number===1}" @click="myKey='BesePassGoals'" type="button" class="text-center text-white font-bold text-sm p-1">پاس گل</button></div>
        <div @click="showKey(3)"><button  :class="{bgGray : number===3 , textWhite : number===2}"  @click="myKey='TotalCompetition'" type="button" class="text-center text-white font-bold text-sm p-1">مجموع</button></div>
      </div>
      <Transition name="fade" :duration="500" mode="out-in">
      <keep-alive>
        <component :is="myKey"></component>
      </keep-alive>
      </Transition>
    </div>
  </div>
</section>

</template>

<script>
import{defineAsyncComponent} from "vue";
const BestGoals=defineAsyncComponent(()=>import('@/components/main/competitions/BestGoals'))
const TotalCompetition=defineAsyncComponent(()=>import('@/components/main/competitions/TotalCompetition'))
const BesePassGoals=defineAsyncComponent(()=>import('@/components/main/competitions/BesePassGoals'))

import{ref} from 'vue'
export default {
  name: "LastCompetition",
  components: {BesePassGoals, BestGoals,TotalCompetition},

  setup(){
    const size=ref(600)
    const myKey=ref('BestGoals')
    const number=ref(1)
    const showKey=(data)=>{
      number.value=data

    }
    return{
      size,
      myKey,
      number,
      showKey,
    }
  }
}
</script>

<style scoped>
.bgGray{
  background-color: grey;
}
.textWhite{
  color: #fff;
}
.myScroll{
  height:auto;
  overflow-y: scroll;
  overflow-x: hidden;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #ffffff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color: grey;
}
#scroll::-webkit-scrollbar-track{
  background-color: lightgrey;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
.fade-enter-active,
.fade-leave-active{
  transition: all ease-in-out 0.5s;
}
.fade-enter-from,
.fade-leave-to{
  transition: inherit;
  opacity: 0;
}
</style>