<template>
  <section class="select-teams">
    <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md md:mx-auto md:w-10/12 max-w-screen-lg lg:mx-1 max-w-screen-xl lg:mx-0 lg:w-full xl:mx-1 max-w-screen-2xl">
      <div class="w-full h-auto">
  <div class="flex flex-row  justify-between p-1">
      <div class="flex flex-row  justify-between w-full">
        <div class="w-2/12 lg:w-2/12 sm:w-1/12"><p class="text-black font-bold text-start text-md">رقابت ها</p></div>
        <div class="w-10/12 lg:w-10/12 sm:w-11/12 flex flex-row align-center justify-between">
          <div class="mx-3">
            <font-awesome-icon :icon="['fas','angle-right']" class="font-thing text-slate-900 mx-2"></font-awesome-icon>
          </div>
        <div v-for="item in list" :key="item">
          <a @click="total(`${item[0].competition_id}`)" class="inline-block p-2 cursor-pointer"><span class="text-slate-800 text-sm sm:text-xs">{{item[0].name_fa}}</span></a>
          <a @click="total(`${item[1].competition_id}`)" class="inline-block p-2 cursor-pointer"><span class="text-slate-800 text-sm sm:text-xs">{{item[1].name_fa}}</span></a>
          <a @click="total(`${item[2].competition_id}`)" class="inline-block p-2 cursor-pointer"><span class="text-slate-800 text-sm sm:text-xs">{{item[2].name_fa}}</span></a>
          <a @click="total(`${item[3].competition_id}`)" class="inline-block p-2 cursor-pointer"><span class="text-slate-800 text-sm sm:text-xs">{{item[3].name_fa}}</span></a>
          <a @click="total(`${item[4].competition_id}`)" class="inline-block p-2 cursor-pointer"><span class="text-slate-800 text-sm sm:text-xs">{{item[4].name_fa}}</span></a>
          <a @click="total(`${item[5].competition_id}`)" class="inline-block p-2 cursor-pointer"><span class="text-slate-800 text-sm sm:text-xs">{{item[5].name_fa}}</span></a>
        </div>
          <div class="mx-3">
            <font-awesome-icon :icon="['fas','angle-left']" class="font-thin text-slate-900 mx-2"></font-awesome-icon>
          </div>
        </div>
      </div>
  </div>
  <div class="bg-slate-800 mb-3 h-0.5 flex flex-row justify-center mx-auto" style="width:90%"></div>

  <div class="w-full h-auto bg-white border border-1 border-slate-400 rounded-md">
    <div class="flex flex-row align-center justify-between lg:flex lg:flex-row lg:items-center lg:justify-between lg:w-full sm:grid sm:grid-cols-1 sm:grid-rows-3 sm:gap-4 sm:w-full md:grid md:grid-cols-1 md:grid-rows-3 md:w-10/12 md:gap-2">
      <div class="w-3/12 lg:mx-0 lg:w-3/12 md:w-10/12 md:mx-auto sm:w-full h-auto">
        <last-competition ></last-competition>
      </div>
      <div class="w-5/12 lg:mx-0 lg:w-5/12 md:w-10/12 md:mx-auto sm:w-full h-auto">
        <table-teams></table-teams>
      </div>
      <div class="w-3/12 lg:mx-0 lg:w-3/12 md:w-10/12 md:mx-auto sm:w-full h-auto">
        <play-program></play-program>
      </div>
    </div>
  </div>
      </div>
    </div>
  </section>
</template>

<script>
import{reactive,onMounted,ref} from 'vue'
import EndPoints from "@/service/EndPoints";
import LastCompetition from "@/components/main/competitions/LastCompetition";
import TableTeams from "@/components/main/TableTeams";
import PlayProgram from "@/components/main/PlayProgram";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "SelectTeams",
  components:{FontAwesomeIcon,TableTeams,PlayProgram,LastCompetition},
  setup(){
    let sum=ref(null)
    const size=ref(14)
    const active=reactive[{
      backgroundColor:'gray',
      color:'white'
    }]
    const size2=ref(11)
    const list=reactive([])
    function total(data){
      sum.value=data
    }
    const result=onMounted(()=>{
      EndPoints.getSelectTeam()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data.standing_tab)
          console.log(list)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      list,
      size2,
      total,
      active,
      sum,
      size,
      result,
    }

  }
}
</script>

<style scoped>
.myScroll{
  height: auto;
  overflow-y: hidden;
  overflow-x: scroll;
}
#scroll::-webkit-scrollbar{
  width: 5px;
}
#scroll::-webkit-scrollbar-thumb{
  background-color:ghostwhite;
}
#scroll::-webkit-scrollbar-track{
  background-color: gray;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.6);
}
</style>