<template>
<section class="center-news-transfer">
  <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md">
    <div class="w-full h-auto">
      <div class="flex flex-row align-center justify-between w-full">
        <Suspense>
          <template #default>
            <NumberTransfer :list="list"/>
          </template>
          <template #fallback>
          <LoaderComponentTwo/>
          </template>
        </Suspense>

        </div>
      </div>
    </div>

</section>
</template>

<script>
import axios from 'axios'
import {reactive,onMounted} from "vue";
import {defineAsyncComponent} from "vue";
import {handleError} from "@/components/composable/handleError";
const NumberTransfer=defineAsyncComponent(()=>import('@/components/main/transfer/NumberTransfer'))
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
export default {
  name: "LastNewsTransfer",
  components: {LoaderComponentTwo, NumberTransfer},
  setup(){
    const list=reactive([])
    const total=onMounted(async ()=>{
      await axios.get('https://api.footballi.net/api/v2/transfer/news')
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data)
        }
      })
      .catch((error)=>{
       handleError(error)
      })
    })

    return{
      list,
      total,
    }
  }
}
</script>

<style scoped>
.message{
  width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
}

</style>